import React, { useState } from "react";
import "./TweetBox.css";
import { Avatar, Button } from "@mui/material";
import db from "./firebase";

function TweetBox() {
  const [tweetUserName, setTweetUserName] = useState("");
  const [tweetDisplayName, setTweetDisplayName] = useState("");
  const [tweetMessage, setTweetMessage] = useState("");
  const [tweetImage, setTweetImage] = useState("");

  const sendTweet = (e) => {
    e.preventDefault();
    if(tweetMessage === "") return;
    db.collection("posts").add({
      displayName: tweetDisplayName? tweetDisplayName : "Dawg",
      username: tweetUserName? tweetUserName : "User_dawg",
      verified: false,
      text: tweetMessage,
      image: tweetImage,
      avatar:
        "data:image/jpeg;base64,",
    });
    setTweetMessage("");
    setTweetImage("");
  };

  return (
    <div className="tweetBox">
      <form>
      <input
          value={tweetDisplayName}
          onChange={(e) => setTweetDisplayName(e.target.value)}
          className="tweetBox__imageInput"
          placeholder="Enter Display name | default: Dawg"
          type="text"
        />
      <input
          value={tweetUserName}
          onChange={(e) => setTweetUserName(e.target.value)}
          className="tweetBox__imageInput"
          placeholder="Enter User name | default: User_dawg"
          type="text"
        />
        <div className="tweetBox__input">
          <Avatar src="data:image/jpeg;base64," />
          <input
            onChange={(e) => setTweetMessage(e.target.value)}
            value={tweetMessage}
            placeholder="What da dog doin?"
            type="text"
          />
        </div>
        <input
          value={tweetImage}
          onChange={(e) => setTweetImage(e.target.value)}
          className="tweetBox__imageInput"
          placeholder="Optional: Enter image URL"
          type="text"
        />

        <Button
          onClick={sendTweet}
          type="submit"
          className="tweetBox__tweetButton"
        >
          Go!
        </Button>
      </form>
    </div>
  );
}

export default TweetBox;